import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sortAge'
})
export class SortAgePipe implements PipeTransform {

  transform(value: any[]): any {
    const arr = value.sort((a,b) => a.age - b.age);
    return arr;
    
  }

}



